(function ( wpI18n, wpHooks, wpElement, wpCompose, wpComponents ) {
    const { __ } = wpI18n;
    const { addFilter } = wpHooks;
    const { Fragment, useState, Component } = wpElement;
    const { createHigherOrderComponent } = wpCompose;
    const { SelectControl } = wpComponents;

    // { content } would also output the contents before this filter. Can be placed below SelectControl
    addFilter( 'advgb.timezoneControl', 'advgb/timezoneOptions', ( content, props ) => {
        const { label, value, defaultTimezone, onChangeTimezone } = props;

        const timezones = typeof advgbBlocks.timezones !== 'undefined' && advgbBlocks.timezones.length
                            ? advgbBlocks.timezones
                            : [];
        const options = timezones.map(timezone => {
            return { value: timezone, label: timezone.replace(/_/g, ' ') };
        });

        return (
            <Fragment>
                <div style={{ marginTop: 10, marginBottom: 30 }}>
                    <div style={{ marginBottom: 6 }}>
                        { label }
                    </div>
                    <SelectControl
                        value={ value }
                        options={ options }
                        onChange={ onChangeTimezone }
                    />
                </div>
            </Fragment>
        );
    });

})( wp.i18n, wp.hooks, wp.element, wp.compose, wp.components );
