(function ( wpI18n, wpHooks, wpBlockEditor, wpComponents, wpElement ) {
    wpBlockEditor = wp.blockEditor || wp.editor;
    const { addFilter } = wpHooks;
    const { __ } = wpI18n;
    const { InspectorControls, BlockControls, AlignmentToolbar } = wpBlockEditor;
    const { PanelBody, RangeControl } = wpComponents;
    const { Fragment } = wpElement;

    // Set attributes
    addFilter( 'blocks.registerBlockType', 'advgb/registerTestimonialProClass', function ( settings ) {
        if ( settings.name === 'advgb/testimonial' ) {
            settings.attributes = Object.assign( settings.attributes, {
                nameSize: {
                    type: 'number'
                },
                positionSize: {
                    type: 'number'
                },
                descriptionSize: {
                    type: 'number'
                },
                align: {
                    type: 'string',
                    default: 'center'
                }
            } );
        }

        return settings;
    } );

    // Add fields to sidebar
    addFilter( 'editor.BlockEdit', 'advgb/testimonialPro', function ( BlockEdit ) {
        return ( props ) => {
            return (
                <Fragment>
                    <BlockEdit {...props} />
                    { props.isSelected && props.name === 'advgb/testimonial' && (
                        <Fragment>
                            <BlockControls>
                                <AlignmentToolbar
                                    value={ props.attributes.align }
                                    onChange={ ( value ) => props.setAttributes( { align: value } ) }
                                />
                            </BlockControls>
                            <InspectorControls>
                                <PanelBody title={ __('Content Size', 'advanced-gutenberg') } className="advgb-pro-icon">
                                    <RangeControl
                                        label={ __( 'Name size', 'advanced-gutenberg' ) }
                                        value={ props.attributes.nameSize || '' }
                                        onChange={ ( value ) => props.setAttributes( { nameSize: value } ) }
                                        min={ 10 }
                                        max={ 100 }
                                        beforeIcon="editor-textcolor"
                                        allowReset
                                    />
                                    <RangeControl
                                        label={ __( 'Position size', 'advanced-gutenberg' ) }
                                        value={ props.attributes.positionSize || '' }
                                        onChange={ ( value ) => props.setAttributes( { positionSize: value } ) }
                                        min={ 10 }
                                        max={ 100 }
                                        beforeIcon="editor-textcolor"
                                        allowReset
                                    />
                                    <RangeControl
                                        label={ __( 'Description size', 'advanced-gutenberg' ) }
                                        value={ props.attributes.descriptionSize || '' }
                                        onChange={ ( value ) => props.setAttributes( { descriptionSize: value } ) }
                                        min={ 10 }
                                        max={ 100 }
                                        beforeIcon="editor-textcolor"
                                        allowReset
                                    />
                                </PanelBody>
                            </InspectorControls>
                        </Fragment>
                    ) }
                    { props.name === 'advgb/testimonial' && props.attributes.pid && (
                        <div>
                            <style>
                            { props.attributes.nameSize && (
                                `#${props.attributes.pid} .advgb-testimonial .advgb-testimonial-name {
                                    font-size: ${props.attributes.nameSize}px;
                                }`
                            ) }
                            { props.attributes.positionSize && (
                                `#${props.attributes.pid} .advgb-testimonial .advgb-testimonial-position {
                                    font-size: ${props.attributes.positionSize}px;
                                }`
                            ) }
                            { props.attributes.descriptionSize && (
                                `#${props.attributes.pid} .advgb-testimonial .advgb-testimonial-desc {
                                    font-size: ${props.attributes.descriptionSize}px;
                                }`
                            ) }
                            { props.attributes.align && (
                                `#${props.attributes.pid} .advgb-testimonial .advgb-testimonial-item {
                                    text-align: ${props.attributes.align};
                                }`
                            ) }
                            </style>
                        </div>
                    )}
                </Fragment>
            )
        }
    } );

})( wp.i18n, wp.hooks, wp.blockEditor, wp.components, wp.element );
