(function ( wpI18n, wpBlocks, wpElement, wpComponents, wpBlockEditor ) {
    wpBlockEditor = wp.blockEditor || wp.editor;
    const { __ } = wpI18n;
    const { createElement, Fragment, Component } = wpElement;
    const { registerBlockType } = wpBlocks;
    const { PanelBody, RangeControl, ToggleControl } = wpComponents;
    const { RichText, InspectorControls, AlignmentToolbar, BlockControls, PanelColorSettings, InnerBlocks } = wpBlockEditor;

    const previewImageData = 'data:image/png;base64,';

    class featureListEdit extends Component {

        constructor() {
            super( ...arguments );
        }

        componentWillMount() {
            const { attributes, setAttributes } = this.props;
            const currentBlockConfig = advgbDefaultConfig['advgb-feature-list'];

            // No override attributes of blocks inserted before
            if (attributes.changed !== true) {
                if (typeof currentBlockConfig === 'object' && currentBlockConfig !== null) {
                    Object.keys(currentBlockConfig).map((attribute) => {
                        if (typeof attributes[attribute] === 'boolean') {
                            attributes[attribute] = !!currentBlockConfig[attribute];
                        } else {
                            attributes[attribute] = currentBlockConfig[attribute];
                        }
                    });
                }

                // Finally set changed attribute to true, so we don't modify anything again
                setAttributes( { changed: true } );
            }
        }

        componentDidMount() {
            const { attributes, setAttributes, clientId } = this.props;
            const { blockID } = attributes;

            setAttributes( { blockID: `advgb-feature-list-${clientId}` } );
        }

        render() {

            const {
                className,
                attributes,
                setAttributes,
            } = this.props;

            const {
                isPreview,
                blockID,
                alignment,
                stripes,
                stripesBackground1,
                stripesBackground2,
                iconSize,
                spacingIconText,
                lineHeight,
                fontSize,
                paddingTop,
                paddingRight,
                paddingBottom,
                paddingLeft,
            } = attributes;

            const featureTemplate = [
                [
                    'advgb/feature',
                    {
                        content: 'Feature 1',
                    }
                ],
                [
                    'advgb/feature',
                    {
                        content: 'Feature 2',
                    }
                ],
                [
                    'advgb/feature',
                    {
                        content: 'Feature 3',
                    }
                ],
                [
                    'advgb/feature',
                    {
                        content: 'Feature 4',
                        icon: 'no',
                        iconColor: '#cf2e2e',
                    }
                ],
            ];

            const listClassName = [
                blockID,
                className,
                alignment && `advgb-feature-list-alignment__${alignment}`,
            ].filter( Boolean ).join( ' ' );

            return (
                isPreview ?
                    <img alt={ __('Feature List', 'advanced-gutenberg') } width='100%' src={ previewImageData }/>
                    :
                    <Fragment>
                        <BlockControls>
                            <AlignmentToolbar
                            value={ alignment } onChange={ ( value ) => setAttributes( { alignment: value === undefined ? 'center' : value } ) } />
                        </BlockControls>
                        <InspectorControls>
                            <PanelBody
                                title={ __( 'Main Settings', 'advanced-gutenberg' ) }
                                initialOpen={ true }
                                >
                                <ToggleControl
                                    label={ __( 'Stripes', 'advanced-gutenberg' ) }
                                    checked={ stripes }
                                    onChange={ () => setAttributes( { stripes: !stripes } ) }
                                />
                                { stripes &&
                                    <PanelColorSettings
                                        title={ __( 'Stripes Colors', 'advanced-gutenberg' ) }
                                        initialOpen={ false }
                                        colorSettings={ [
                                            {
                                                label: __( 'Odd Background', 'advanced-gutenberg' ),
                                                value: stripesBackground1,
                                                onChange: ( value ) => setAttributes( { stripesBackground1: value === undefined ? '#ffffff' : value } ),
                                            },
                                            {
                                                label: __( 'Even Background', 'advanced-gutenberg' ),
                                                value: stripesBackground2,
                                                onChange: ( value ) => setAttributes( { stripesBackground2: value === undefined ? '#f2f2f2' : value } ),
                                            },
                                        ] }
                                    />
                                }
                            </PanelBody>
                            <PanelBody
                                title={ __( 'Feature Settings', 'advanced-gutenberg' ) }
                                initialOpen={ true }
                                >
                                <RangeControl
                                    label={__('Text size', 'advanced-gutenberg')}
                                    value={fontSize || ''}
                                    onChange={(size) => setAttributes({fontSize: size})}
                                    min={10}
                                    max={100}
                                    beforeIcon="editor-textcolor"
                                    allowReset
                                />
                                <RangeControl
                                    label={__('Icon size', 'advanced-gutenberg')}
                                    value={iconSize || ''}
                                    onChange={(size) => setAttributes({iconSize: size})}
                                    min={10}
                                    max={100}
                                    allowReset
                                />
                                <RangeControl
                                    label={__('Line height', 'advanced-gutenberg')}
                                    value={lineHeight || ''}
                                    onChange={(size) => setAttributes({lineHeight: size})}
                                    min={0}
                                    max={100}
                                    allowReset
                                />
                                <RangeControl
                                    label={__('Spacing between icon and text', 'advanced-gutenberg')}
                                    value={spacingIconText || ''}
                                    onChange={(size) => setAttributes({spacingIconText: size})}
                                    min={0}
                                    max={100}
                                    allowReset
                                />
                                <RangeControl
                                    label={ __( 'Padding top', 'advanced-gutenberg' ) }
                                    value={ paddingTop || '' }
                                    onChange={ ( value ) => setAttributes( { paddingTop: value } ) }
                                    min={ 0 }
                                    max={ 100 }
                                />
                                <RangeControl
                                    label={ __( 'Padding right', 'advanced-gutenberg' ) }
                                    value={ paddingRight || '' }
                                    onChange={ ( value ) => setAttributes( { paddingRight: value } ) }
                                    min={ 0 }
                                    max={ 100 }
                                />
                                <RangeControl
                                    label={ __( 'Padding bottom', 'advanced-gutenberg' ) }
                                    value={ paddingBottom || '' }
                                    onChange={ ( value ) => setAttributes( { paddingBottom: value } ) }
                                    min={ 0 }
                                    max={ 100 }
                                />
                                <RangeControl
                                    label={ __( 'Padding left', 'advanced-gutenberg' ) }
                                    value={ paddingLeft || '' }
                                    onChange={ ( value ) => setAttributes( { paddingLeft: value } ) }
                                    min={ 0 }
                                    max={ 100 }
                                />
                            </PanelBody>
                        </InspectorControls>
                        <div className={ listClassName }>
                            <InnerBlocks
                                template={ featureTemplate }
                                templateLock={ false }
                                allowedBlocks={ [
                                    'advgb/feature',
                                ] }
                                className={ listClassName }
                            />
                        </div>
                        <style>
                            {`.${blockID}.wp-block-advgb-feature-list .wp-block-advgb-feature {
                                font-size: ${fontSize}px;
                                line-height: ${lineHeight}px;
                                padding: ${paddingTop}px ${paddingRight}px ${paddingBottom}px ${paddingLeft}px;
                            }
                            .${blockID}.wp-block-advgb-feature-list .wp-block-advgb-feature:before {
                                font-size: ${iconSize}px;
                                margin-right: ${spacingIconText}px;
                            }`}
                        </style>
                        { stripes &&
                            <style>
                            { `.${blockID}.wp-block-advgb-feature-list .block-editor-block-list__layout > div:nth-child(2n+1) {
                                background: ${stripesBackground1};
                            }
                            .${blockID}.wp-block-advgb-feature-list .block-editor-block-list__layout > div {
                                background: ${stripesBackground2};
                            }` }
                            </style>
                        }
                    </Fragment>
            );
        }
    }

    const blockIcon = (
        <svg xmlns="http://www.w3.org/2000/svg" enable-background="new 0 0 24 24" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000">
            <g>
                <rect fill="none" height="24" width="24"/>
            </g>
            <g>
                <g>
                    <path d="M20,3H4C2.9,3,2,3.9,2,5v14c0,1.1,0.9,2,2,2h16c1.1,0,2-0.9,2-2V5 C22,3.9,21.1,3,20,3z M20,19H4V5h16V19z" fill-rule="evenodd"/>
                    <polygon fill-rule="evenodd" points="19.41,10.42 17.99,9 14.82,12.17 13.41,10.75 12,12.16 14.82,15"/>
                    <rect fill-rule="evenodd" height="2" width="5" x="5" y="7"/>
                    <rect fill-rule="evenodd" height="2" width="5" x="5" y="11"/>
                    <rect fill-rule="evenodd" height="2" width="5" x="5" y="15"/>
                </g>
            </g>
        </svg>
    )
    const blockAttrs = {
        blockID: {
            type: 'string',
        },
        alignment: {
            type: 'string',
            default: 'center',
        },
        stripes: {
            type: 'boolean',
            default: true,
        },
        stripesBackground1: {
            type: 'string',
            default: '#ffffff',
        },
        stripesBackground2: {
            type: 'string',
            default: '#f2f2f2',
        },
        fontSize: {
            type: 'number',
            default: 18,
        },
        iconSize: {
            type: 'number',
            default: 22,
        },
        lineHeight: {
            type: 'number',
            default: 30,
        },
        spacingIconText: {
            type: 'number',
            default: 5,
        },
        paddingTop: {
            type: 'number',
            default: 15,
        },
        paddingRight: {
            type: 'number',
            default: 20,
        },
        paddingBottom: {
            type: 'number',
            default: 15,
        },
        paddingLeft: {
            type: 'number',
            default: 20,
        },
        changed: {
            type: 'boolean',
            default: false,
        },
        isPreview: {
            type: 'boolean',
            default: false,
        },
    };

    registerBlockType('advgb/feature-list', {
        title: __('Feature List - PublishPress', 'advanced-gutenberg'),
        description: __('Show your visitors a list of features. This is ideal for pricing and comparison tables.', 'advanced-gutenberg'),
        icon: {
            src: blockIcon,
            foreground: typeof advgbBlocks !== 'undefined' ? advgbBlocks.color : undefined,
        },
        category: 'advgb-category',
        keywords: [
            __( 'feature list', 'advanced-gutenberg' ),
            __( 'features', 'advanced-gutenberg' ),
            __( 'list', 'advanced-gutenberg' ),
        ],
        attributes: blockAttrs,
        example: {
            attributes: {
                isPreview: true
            },
        },
        edit: featureListEdit,
        save: function ( { attributes } ) {
            const {
                className,
                alignment,
                stripes,
                stripesBackground1,
                stripesBackground2,
                iconSize,
                spacingIconText,
                lineHeight,
                fontSize,
                paddingTop,
                paddingRight,
                paddingBottom,
                paddingLeft,
                blockID,
            } = attributes;

            const listClassName = [
                blockID,
                className,
                alignment && `advgb-feature-list-alignment__${alignment}`,
            ].filter( Boolean ).join( ' ' );

            return (
                <div className={ listClassName }>
                    <InnerBlocks.Content />
                </div>
            );
        },
    });
})( wp.i18n, wp.blocks, wp.element, wp.components, wp.blockEditor );
