(function ( wpI18n, wpBlocks, wpElement, wpComponents, wpBlockEditor ) {
    wpBlockEditor = wp.blockEditor || wp.editor;
    const { __ } = wpI18n;
    const { createElement, Fragment, Component } = wpElement;
    const { registerBlockType } = wpBlocks;
    const { PanelBody, BaseControl, Dashicon } = wpComponents;
    const { RichText, InspectorControls, ColorPalette } = wpBlockEditor;

    const previewImageData = 'data:image/png;base64,';

    class featureEdit extends Component {

        constructor() {
            super( ...arguments );
        }

        componentWillMount() {
            const { attributes, setAttributes } = this.props;
            const currentBlockConfig = advgbDefaultConfig['advgb-feature'];

            // No override attributes of blocks inserted before
            if (attributes.changed !== true) {
                if (typeof currentBlockConfig === 'object' && currentBlockConfig !== null) {
                    Object.keys(currentBlockConfig).map((attribute) => {
                        if (typeof attributes[attribute] === 'boolean') {
                            attributes[attribute] = !!currentBlockConfig[attribute];
                        } else {
                            attributes[attribute] = currentBlockConfig[attribute];
                        }
                    });
                }

                // Finally set changed attribute to true, so we don't modify anything again
                setAttributes( { changed: true } );
            }
        }

        componentDidMount() {
            const { attributes, setAttributes, clientId } = this.props;
            const { blockID } = attributes;

            setAttributes( { blockID: `advgb-feature-${clientId}` } );
        }

        render() {

            const {
                className,
                attributes,
                setAttributes,
            } = this.props;

            const {
                isPreview,
                blockID,
                content,
                icon,
                iconColor,
            } = attributes;

            const listIcons = [
                {label: __('None', 'advanced-gutenberg'), value: ''},
                {label: __('Pushpin', 'advanced-gutenberg'), value: 'admin-post'},
                {label: __('Configuration', 'advanced-gutenberg'), value: 'admin-generic'},
                {label: __('Flag', 'advanced-gutenberg'), value: 'flag'},
                {label: __('Star', 'advanced-gutenberg'), value: 'star-filled'},
                {label: __('Checkmark', 'advanced-gutenberg'), value: 'yes'},
                {label: __('Checkmark 2', 'advanced-gutenberg'), value: 'yes-alt'},
                {label: __('Checkmark 3', 'advanced-gutenberg'), value: 'saved'},
                {label: __('Minus', 'advanced-gutenberg'), value: 'minus'},
                {label: __('Minus 2', 'advanced-gutenberg'), value: 'remove'},
                {label: __('Plus', 'advanced-gutenberg'), value: 'plus'},
                {label: __('Plus 2', 'advanced-gutenberg'), value: 'insert'},
                {label: __('Play', 'advanced-gutenberg'), value: 'controls-play'},
                {label: __('Arrow right', 'advanced-gutenberg'), value: 'arrow-right-alt'},
                {label: __('Arrow right 2', 'advanced-gutenberg'), value: 'arrow-right-alt2'},
                {label: __('X Cross 2', 'advanced-gutenberg'), value: 'no'},
                {label: __('X Cross', 'advanced-gutenberg'), value: 'dismiss'},
                {label: __('Warning', 'advanced-gutenberg'), value: 'warning'},
                {label: __('Help', 'advanced-gutenberg'), value: 'editor-help'},
                {label: __('Info', 'advanced-gutenberg'), value: 'info'},
                {label: __('Info 2', 'advanced-gutenberg'), value: 'info-outline'},
                {label: __('Circle', 'advanced-gutenberg'), value: 'marker'},
            ];

            const listClassName = [
                blockID,
                className,
                icon && `advgb-feature-${icon}`,
            ].filter( Boolean ).join( ' ' );

            return (
                isPreview ?
                    <img alt={ __('Feature', 'advanced-gutenberg') } width='100%' src={ previewImageData }/>
                    :
                    <Fragment>
                        <InspectorControls>
                            <PanelBody title={__('Icon Settings', 'advanced-gutenberg')}>
                                <BaseControl label={__('List icon', 'advanced-gutenberg')}>
                                    <div className="advgb-icon-items-wrapper">
                                        {listIcons.map((item, index) => (
                                            <div className="advgb-icon-item h20" key={index}>
                                            <span onClick={() => setAttributes({icon: item.value})}
                                                  className={[
                                                      item.value === icon && 'active',
                                                      item.value === '' && 'remove-icon',
                                                  ].filter(Boolean).join(' ')}
                                            >
                                                <Dashicon icon={item.value}/>
                                            </span>
                                            </div>
                                        ))}
                                    </div>
                                </BaseControl>
                                { icon && (
                                    <Fragment>
                                        <PanelBody
                                            title={[
                                                __('Icon color', 'advanced-gutenberg'),
                                                <span key="advgb-list-icon-color"
                                                      className={`dashicons dashicons-${icon}`}
                                                      style={{color: iconColor, marginLeft: '10px'}}/>
                                            ]}
                                            initialOpen={ true }
                                        >
                                            <ColorPalette
                                                value={iconColor}
                                                onChange={(value) => setAttributes({iconColor: value === undefined ? '#08ba79' : value})}
                                            />
                                        </PanelBody>
                                    </Fragment>
                                ) }
                            </PanelBody>
                        </InspectorControls>
                        <RichText
                            tagName="div"
                            onChange={ (value) => setAttributes( { content: value } ) }
                            value={ content }
                            className={ listClassName }
                            placeholder={ __('Write a feature', 'advanced-gutenberg') }
                            allowedFormats={ [
                                'core/bold',
                                'core/italic',
                                'core/text-color',
                            ] }
                        />
                        { icon &&
                            <style>
                                {`.${blockID}:before {
                                    color: ${iconColor};
                                }` }
                            </style>
                        }
                    </Fragment>
            );
        }
    }

    const blockIcon = (
        <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000">
            <path d="M0 0h24v24H0V0z" fill="none"/>
            <path d="M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z"/>
        </svg>
    )
    const blockAttrs = {
        blockID: {
            type: 'string',
        },
        content: {
            type: 'string',
            default: '',
        },
        icon: {
            type: 'string',
            default: 'saved',
        },
        iconColor: {
            type: 'string',
            default: '#08ba79',
        },
        changed: {
            type: 'boolean',
            default: false,
        },
        isPreview: {
            type: 'boolean',
            default: false,
        },
    };

    registerBlockType('advgb/feature', {
        title: __('Feature', 'advanced-gutenberg'),
        description: __('Insert a feature inside Feature List block.', 'advanced-gutenberg'),
        icon: {
            src: blockIcon,
            foreground: typeof advgbBlocks !== 'undefined' ? advgbBlocks.color : undefined,
        },
        parent: [ 'advgb/feature-list' ],
        category: 'advgb-category',
        keywords: [
            __( 'features', 'advanced-gutenberg' ),
        ],
        attributes: blockAttrs,
        example: {
            attributes: {
                isPreview: true
            },
        },
        edit: featureEdit,
        save: function ( { attributes } ) {
            const {
                blockID,
                className,
                content,
                icon,
                iconColor,
            } = attributes;

            const listClassName = [
                blockID,
                className,
                icon && `advgb-feature-${icon}`,
            ].filter( Boolean ).join( ' ' );

            return (
                <RichText.Content
                    tagName="div"
                    value={ content }
                    className={ listClassName }
                />
            );
        },
    });
})( wp.i18n, wp.blocks, wp.element, wp.components, wp.blockEditor );
