import { AdvColorControl } from "../../../../../PublishPress-Blocks/src/assets/blocks/0-adv-components/components.jsx";

(function ( wpI18n, wpHooks, wpBlockEditor, wpComponents, wpElement ) {
    wpBlockEditor = wp.blockEditor || wp.editor;
    const { addFilter } = wpHooks;
    const { __ } = wpI18n;
    const { InspectorControls } = wpBlockEditor;
    const { PanelBody } = wpComponents;
    const { Fragment } = wpElement;

    // Set attributes
    addFilter( 'blocks.registerBlockType', 'advgb/registerListProClass', function ( settings ) {
        if ( settings.name === 'advgb/list' ) {
            settings.attributes = Object.assign( settings.attributes, {
                textColor: {
                    type: 'string'
                }
            } );
        }

        return settings;
    } );

    // Add fields to sidebar
    addFilter( 'editor.BlockEdit', 'advgb/listPro', function ( BlockEdit ) {
        return ( props ) => {
            return (
                <Fragment>
                    <BlockEdit {...props} />
                    { props.isSelected && props.name === 'advgb/list' && (
                        <Fragment>
                            <InspectorControls>
                                <PanelBody title={ __('Colors', 'advanced-gutenberg') } initialOpen={ true } className="advgb-pro-icon">
                                    <AdvColorControl
                                        label={ __('Text Color', 'advanced-gutenberg') }
                                        value={ props.attributes.textColor }
                                        onChange={ (value) => props.setAttributes( { textColor: value } ) }
                                    />
                                </PanelBody>
                            </InspectorControls>
                        </Fragment>
                    ) }
                    { props.name === 'advgb/list' && props.attributes.id && (
                        <div>
                            { props.attributes.textColor &&
                                <style>
                                    {`.${props.attributes.id} li {
                                        color: ${props.attributes.textColor};
                                    }`}
                                </style>
                            }
                        </div>
                    ) }
                </Fragment>
            )
        }
    } );

})( wp.i18n, wp.hooks, wp.blockEditor, wp.components, wp.element );
