import { AdvFontControl, AdvLoadGoogleStylesheet, AdvSetGoogleFontStyle } from "../0-adv-components/fonts.jsx";

(function ( wpI18n, wpHooks, wpBlockEditor, wpComponents, wpElement ) {
    wpBlockEditor = wp.blockEditor || wp.editor;
    const { addFilter } = wpHooks;
    const { __, sprintf } = wpI18n;
    const { InspectorControls } = wpBlockEditor;
    const { PanelBody, RangeControl, SelectControl, Button, Notice } = wpComponents;
    const { Fragment, useEffect, useState } = wpElement;

    // Set attributes
    addFilter( 'blocks.registerBlockType', 'advgb/registerAdvTabsProClass', function ( settings ) {
        if ( settings.name === 'advgb/adv-tabs' ) {
            settings.attributes = Object.assign( settings.attributes, {
                fontSize: {
                    type: 'number'
                },
                fontFamily: {
                    type: 'string',
                },
                fontVariation: {
                    type: 'string',
                },
                textTransform: {
                    type: 'string',
                    default: '',
                },
                preset: {
                    type: 'string',
                    default: 'default',
                },
                spacingBelow: {
                    type: 'number',
                    default: 8,
                },
                spacingRight: {
                    type: 'number',
                    default: 8,
                }
            } );
        }

        return settings;
    } );

    // Add fields to sidebar
    addFilter( 'editor.BlockEdit', 'advgb/advTabsPro', function ( BlockEdit ) {
        return ( props ) => {
            const { attributes, setAttributes, clientId } = props;
            const {
                preset,
                fontSize,
                fontFamily,
                fontVariation,
                textTransform,
                spacingBelow,
                spacingRight,
                activeTabBgColor,
                tabsStyleD,
                borderWidth,
                borderColor,
                pid
            } = attributes;
            const [prevTabsStyleD, setTabsStyleD] = useState( tabsStyleD );
            const [prevBorderWidth, setBorderWidth] = useState( borderWidth );

            /* Detect when tabsStyleD changes from vertical to horizontal or viceversa,
             * to adjust spacingBelow or spacingRight if required for Folder theme preset
             */
            useEffect( () => {
                if( prevTabsStyleD !== tabsStyleD ) {

                    setTabsStyleD( tabsStyleD );

                    if( spacingBelow < 0 || spacingRight < 0 ) {

                        if( tabsStyleD === 'vert' && spacingBelow < 0 && spacingRight >= 0 ) {

                            // Invert spacing values to have the negative one in the right place
                            setAttributes( {
                                spacingBelow: spacingRight,
                                spacingRight: spacingBelow,
                            } );

                        } else if( tabsStyleD === 'horz' && spacingRight < 0 && spacingBelow >= 0 ) {

                            // Invert spacing values to have the negative one in the right place
                            setAttributes( {
                                spacingRight: spacingBelow,
                                spacingBelow: spacingRight,
                            } );
                        } else {
                            // Nothing to do here
                        }
                    }
                }
            }, [tabsStyleD]);

            /* Detect when borderWidth changes to adjust spacingBelow or spacingRight
             * if required for Folder theme preset
             */
            useEffect( () => {
                if( prevBorderWidth !== borderWidth ) {

                    setBorderWidth( borderWidth );

                    if( spacingBelow < 0 || spacingRight < 0 ) {

                        if( tabsStyleD === 'vert' && spacingRight < 0 && spacingBelow >= 0 ) {

                            // Update spacingRight with negative borderWidth
                            setAttributes( {
                                spacingRight: -Math.abs(borderWidth),
                            } );

                        } else if( tabsStyleD === 'horz' && spacingBelow < 0 && spacingRight >= 0 ) {

                            // Update spacingBelow with negative borderWidth
                            setAttributes( {
                                spacingBelow: -Math.abs(borderWidth),
                            } );
                        } else {
                            // Nothing to do here
                        }
                    }
                }
            }, [borderWidth]);

            const PRESET_OPTIONS = [
                { label: __( 'Default', 'advanced-gutenberg' ), value: 'default' },
                { label: __( 'Folder', 'advanced-gutenberg' ), value: 'folder' },
                { label: __( 'Square', 'advanced-gutenberg' ), value: 'square' },
                { label: __( 'Cartoon', 'advanced-gutenberg' ), value: 'cartoon' },
            ];

            const currentPreset = PRESET_OPTIONS.find( option => option.value === preset );

            /**
             * Change set of attributes to generate a different design
             *
             * @since 3.1.4
             *
             * @param {string} value    Preset value
             * @param {object} props    Block properties
             *
             * @return {void}
             */
            const setPreset = ( value, props ) => {

                switch( value ) {

                    case 'default':

                        setAttributes( {
                            preset: value,
                            activeTabBgColor: '#5954d6',
                            activeTabTextColor: '#fff',
                            headerBgColor: '#e0e0e0',
                            headerTextColor: '#fff',
                            bodyBgColor: '#fff',
                            bodyTextColor: '',
                            borderRadius: 10,
                            borderStyle: 'solid',
                            borderWidth: 1,
                            borderColor: '#e0e0e0',
                            spacingBelow: 8,
                            spacingRight: 8,
                            fontSize: 19,
                            fontFamily: '',
                            fontVariation: '',
                            tabsStyleD: 'horz'
                        } );

                        break;

                    case 'folder':

                        setAttributes( {
                            preset: value,
                            activeTabBgColor: '#fff',
                            activeTabTextColor: '#655997',
                            headerBgColor: '#655997',
                            headerTextColor: '#fff',
                            bodyBgColor: '#fff',
                            bodyTextColor: '#333',
                            borderRadius: 0,
                            borderStyle: 'solid',
                            borderWidth: 2,
                            borderColor: '#655997',
                            spacingBelow: -2,
                            spacingRight: 3,
                            fontFamily: '',
                            fontVariation: '',
                            tabsStyleD: 'horz'
                        } );

                        break;

                    case 'square':

                        setAttributes( {
                            preset: value,
                            activeTabBgColor: '#fff',
                            activeTabTextColor: '#655997',
                            headerBgColor: '#655997',
                            headerTextColor: '#fff',
                            bodyBgColor: '#fff',
                            bodyTextColor: '#333',
                            borderRadius: 0,
                            borderStyle: 'solid',
                            borderWidth: 1,
                            borderColor: '#655997',
                            spacingBelow: 3,
                            spacingRight: 3,
                            fontFamily: '',
                            fontVariation: '',
                            tabsStyleD: 'horz'
                        } );

                        break;

                    case 'cartoon':

                        setAttributes( {
                            preset: value,
                            activeTabBgColor: '#227db6',
                            activeTabTextColor: '#fff',
                            headerBgColor: '#eb505e',
                            headerTextColor: '#fff',
                            bodyBgColor: '#fff',
                            bodyTextColor: '#333',
                            borderRadius: 10,
                            borderStyle: 'solid',
                            borderWidth: 3,
                            borderColor: '#28303D',
                            spacingBelow: 8,
                            spacingRight: 8,
                            fontSize: 19,
                            fontFamily: 'Lobster',
                            fontVariation: '',
                            tabsStyleD: 'horz'
                        } );

                        break;

                    default:

                        // Nothing to do here
                        break;
                }
            }

            return (
                <Fragment>
                    <BlockEdit {...props} />
                    { props.isSelected && props.name === 'advgb/adv-tabs' && (
                    <Fragment>
                        <InspectorControls>
                            <PanelBody title={ __('Theme Settings', 'advanced-gutenberg') } className="advgb-pro-icon" initialOpen={ false }>
                                <SelectControl
                                    label={ __( 'Preset', 'advanced-gutenberg' ) }
                                    value={ preset }
                                    options={ PRESET_OPTIONS }
                                    onChange={ ( value ) => {
                                        setPreset( value, props )
                                    } }
                                />
                                { typeof currentPreset !== 'undefined' && preset !== 'default' && (
                                    <Button
                                        className="button button-large advgb-browse-image-btn"
                                        onClick={ () => setPreset( preset, props ) }
                                    >
                                        { sprintf(
                                            __( 'Reset %s preset', 'advanced-gutenberg' ),
                                            currentPreset.label
                                        ) }
                                    </Button>
                                ) }
                                <Notice
                                    className="advgb-notice-sidebar"
                                    status="warning"
                                    isDismissible={ false }
                                >
                                    { __( 'Selecting or resetting a preset will override your current design configuration.', 'advanced-gutenberg' ) }
                                </Notice>
                            </PanelBody>
                            <PanelBody title={ __('Tab Settings', 'advanced-gutenberg') } className="advgb-pro-icon" initialOpen={ false }>
                                <AdvFontControl
                                    familyValue={ fontFamily }
                                    familyOnChange={ ( value ) => setAttributes( { fontFamily: value, fontVariation: '' } ) }
                                    variationValue={ fontVariation }
                                    variationOnChange={ ( value ) => setAttributes( { fontVariation: value } ) }
                                    transformValue={ textTransform }
                                    transformOnChange={ ( value ) => setAttributes( { textTransform: value } ) }
                                />
                                <RangeControl
                                    label={ __( 'Font Size', 'advanced-gutenberg' ) }
                                    value={ fontSize || '' }
                                    onChange={ ( value ) => setAttributes( { fontSize: value } ) }
                                    min={ 7 }
                                    max={ 100 }
                                    beforeIcon="editor-textcolor"
                                    allowReset
                                />
                                <RangeControl
                                    label={ __( 'Spacing below', 'advanced-gutenberg' ) }
                                    help={
                                        tabsStyleD === 'horz'
                                            ? __( 'You can set a negative spacing below that depends on border width', 'advanced-gutenberg' )
                                            : ''
                                    }
                                    value={ spacingBelow || '' }
                                    onChange={ ( value ) => setAttributes( { spacingBelow: value } ) }
                                    min={
                                        tabsStyleD === 'horz' && borderWidth !== undefined
                                            ? -Math.abs(borderWidth)
                                            : 0
                                    }
                                    max={ 30 }
                                    beforeIcon="arrow-down-alt"
                                    allowReset
                                />
                                <RangeControl
                                    label={ __( 'Spacing on the right', 'advanced-gutenberg' ) }
                                    help={
                                        tabsStyleD === 'vert'
                                            ? __( 'You can set a negative spacing on the right that depends on border width', 'advanced-gutenberg' )
                                            : ''
                                    }
                                    value={ spacingRight || '' }
                                    onChange={ ( value ) => setAttributes( { spacingRight: value } ) }
                                    min={
                                        tabsStyleD === 'vert' && borderWidth !== undefined
                                            ? -Math.abs(borderWidth)
                                            : 0
                                    }
                                    max={ 30 }
                                    beforeIcon="arrow-right-alt"
                                    allowReset
                                />
                            </PanelBody>
                        </InspectorControls>
                    </Fragment>
                    ) }
                    { props.name === 'advgb/adv-tabs' && pid && (
                        <div>
                            <style>
                            { `#block-${clientId} ul.advgb-tabs-panel li.advgb-tab:not(.advgb-add-tab) {
                                font-size: ${fontSize}px;
                            }` }
                            { ( spacingBelow !== 8 || spacingRight !== 8 ) && (
                                `#block-${clientId} ul.advgb-tabs-panel li.advgb-tab {
                                    margin-bottom: ${spacingBelow}px;
                                    margin-right: ${spacingRight}px;
                                }`
                            ) }
                            { /* Horizontal Tabs
                               * Make border-bottom-color the same as activeTabBgColor
                               * Fix empty border "triangle"
                               */ }
                            { tabsStyleD === 'horz' && spacingBelow < 0 && spacingRight >= 0 && (
                                `#block-${clientId} ul.advgb-tabs-panel li.advgb-tab.advgb-tab-active {
                                    border-bottom-color: ${activeTabBgColor || '#5954d6'} !important;
                                }
                                #block-${clientId} ul.advgb-tabs-panel li.advgb-tab.advgb-tab-active::before,
                                #block-${clientId} ul.advgb-tabs-panel li.advgb-tab.advgb-tab-active::after {
                                    content: "";
                                    display: block;
                                    position: absolute;
                                    background: ${borderColor || '#e0e0e0'};
                                    width: ${borderWidth}px;
                                    height: ${borderWidth}px;
                                }
                                #block-${clientId} ul.advgb-tabs-panel li.advgb-tab.advgb-tab-active::before {
                                  left: -${borderWidth}px;
                                  bottom: -${borderWidth}px;
                                  right: unset;
                                  top: unset;
                                }
                                #block-${clientId} ul.advgb-tabs-panel li.advgb-tab.advgb-tab-active::after {
                                  right: -${borderWidth}px;
                                  bottom: -${borderWidth}px;
                                  left: unset;
                                  top: unset;
                                }`
                            ) }
                            { /* Vertical Tabs
                               * Make border-right-color the same as activeTabBgColor
                               * Fix empty border "triangle"
                               */ }
                            { tabsStyleD === 'vert' && spacingRight < 0 && spacingBelow >= 0 && (
                                `#block-${clientId} ul.advgb-tabs-panel li.advgb-tab.advgb-tab-active {
                                    border-right-color: ${activeTabBgColor || '#5954d6'} !important;
                                }
                                #block-${clientId} ul.advgb-tabs-panel li.advgb-tab.advgb-tab-active::before,
                                #block-${clientId} ul.advgb-tabs-panel li.advgb-tab.advgb-tab-active::after {
                                    content: "";
                                    display: block;
                                    position: absolute;
                                    background: ${borderColor || '#e0e0e0'};
                                    width: ${borderWidth}px;
                                    height: ${borderWidth}px;
                                }
                                #block-${clientId} ul.advgb-tabs-panel li.advgb-tab.advgb-tab-active::before {
                                  right: -${borderWidth}px;
                                  top: -${borderWidth}px;
                                  left: unset;
                                  bottom: unset;
                                }
                                #block-${clientId} ul.advgb-tabs-panel li.advgb-tab.advgb-tab-active::after {
                                  right: -${borderWidth}px;
                                  bottom: -${borderWidth}px;
                                  left: unset;
                                  top: unset;
                                }`
                            ) }
                            { /* Tab CSS */ }
                            { <AdvSetGoogleFontStyle
                                fontFamily={ fontFamily }
                                fontVariation={ fontVariation }
                                textTransform={ textTransform }
                                cssSelector= { `#block-${clientId} ul.advgb-tabs-panel li.advgb-tab:not(.advgb-add-tab)` }
                            /> }
                            </style>
                            { /* Tab Google font stylesheet */ }
                            <AdvLoadGoogleStylesheet
                                fontFamily={ fontFamily }
                                fontVariation={ fontVariation }
                            />
                        </div>
                    ) }
                </Fragment>
            )
        }
    } );

})( wp.i18n, wp.hooks, wp.blockEditor, wp.components, wp.element );
