import { AdvFontControl, AdvLoadGoogleStylesheet, AdvSetGoogleFontStyle } from "../0-adv-components/fonts.jsx";

(function ( wpI18n, wpHooks, wpComponents, wpElement, wpBlockEditor, wpData, wpCompose ) {
    const { __ } = wpI18n;
    const { addFilter } = wpHooks;
    const { RangeControl, PanelBody, SelectControl, Notice, Button } = wpComponents;
    const { Fragment } = wpElement;
    const { InspectorControls } = wpBlockEditor;
    const { withDispatch, select, dispatch } = wpData;
    const { compose } = wpCompose;
    const { times } = lodash;

    // Set attributes
    addFilter( 'blocks.registerBlockType', 'advgb/registerAdvAccordionProClass', function ( settings ) {
        if ( settings.name === 'advgb/accordions' ) {
            settings.attributes = Object.assign( settings.attributes, {
                headerSize: {
                    type: 'number',
                },
                fontFamily: {
                    type: 'string',
                },
                fontVariation: {
                    type: 'string',
                },
                textTransform: {
                    type: 'string',
                    default: '',
                },
                preset: {
                    type: 'string',
                    default: 'default',
                },
            } );
        }

        return settings;
    } );

    // Add fields to sidebar
    addFilter( 'editor.BlockEdit', 'advgb/advAccordionsPro', function ( BlockEdit ) {
        return withDispatch( ( dispatch, { clientId }, { select } ) => {
            const { getBlock } = select( 'core/block-editor' );
            const { updateBlockAttributes } = dispatch( 'core/block-editor' );
            const block = getBlock( clientId );
            return {
                updateAccordionAttributes( attrs ) {
                    times( block.innerBlocks.length, n => {
                        updateBlockAttributes( block.innerBlocks[ n ].clientId, attrs );
                    } );
                },
            };
        })( ( props ) => {

            const { setAttributes, attributes, clientId, updateAccordionAttributes } = props;
            const {
                headerSize,
                id,
                preset,
                fontFamily,
                fontVariation,
                textTransform
            } = attributes;

            const PRESET_OPTIONS = [
                { label: __( 'Default', 'advanced-gutenberg' ), value: 'default' },
                { label: __( 'Square', 'advanced-gutenberg' ), value: 'square' },
                { label: __( 'Cartoon', 'advanced-gutenberg' ), value: 'cartoon' },
            ];

            const currentPreset = PRESET_OPTIONS.find( option => option.value === preset );

            /**
             * Update attributes for parent and inner blocks
             *
             * @since 3.1.4
             *
             * @param {object} attrs Attributes object to update
             *
             * @return {void}
             */
            const updateAllAttrs = ( attrs ) => {

                // Update attributes in Parent block
                setAttributes( attrs );

                // Update attributes in innerBlocks
                updateAccordionAttributes( attrs );
            }

            /**
             * Change set of attributes to generate a different design
             *
             * @since 3.1.4
             *
             * @param {string} value    Preset value
             *
             * @return {void}
             */
            const setPreset = ( value ) => {

                let attrs = {};

                switch( value ) {

                    case 'default':

                        attrs = {
                            preset: value,
                            headerBgColor: '#000',
                            headerTextColor: '#eee',
                            headerIcon: 'unfold',
                            headerIconColor: '#fff',
                            borderStyle: 'solid',
                            borderWidth: 1,
                            borderColor: '#ccc',
                            borderRadius: 2,
                            marginBottom: 15,
                            fontFamily: '',
                            fontVariation: '',
                        };

                        updateAllAttrs( attrs );

                        break;

                    case 'square':

                        attrs = {
                            preset: value,
                            headerBgColor: '#655997',
                            headerTextColor: '#fff',
                            headerIcon: 'unfold',
                            headerIconColor: '#fff',
                            borderStyle: 'solid',
                            borderWidth: 2,
                            borderColor: '#655997',
                            borderRadius: 0,
                            marginBottom: 10,
                            fontFamily: '',
                            fontVariation: '',
                        };

                        updateAllAttrs( attrs );

                        break;

                    case 'cartoon':

                        attrs = {
                            preset: value,
                            headerBgColor: '#EB505E',
                            headerTextColor: '#fff',
                            headerIcon: 'unfold',
                            headerIconColor: '#fff',
                            borderStyle: 'solid',
                            borderWidth: 3,
                            borderColor: '#28303D',
                            borderRadius: 10,
                            marginBottom: 10,
                            fontFamily: 'Lobster',
                            fontVariation: '',
                        };

                        updateAllAttrs( attrs );

                        break;

                    default:

                        // Nothing to do here
                        break;
                }
            }

            return (
                <Fragment>
                    <BlockEdit {...props} />
                    { props.isSelected && props.name === 'advgb/accordions' && (
                        <Fragment>
                            <InspectorControls>
                                <PanelBody title={ __('Theme Settings', 'advanced-gutenberg') } className="advgb-pro-icon" initialOpen={ false }>
                                    <SelectControl
                                        label={ __( 'Preset', 'advanced-gutenberg' ) }
                                        value={ preset }
                                        options={ PRESET_OPTIONS }
                                        onChange={ ( value ) => {
                                            setPreset( value, props )
                                        } }
                                    />
                                    { typeof currentPreset !== 'undefined' && preset !== 'default' && (
                                        <Button
                                            className="button button-large advgb-browse-image-btn"
                                            onClick={ () => setPreset( preset ) }
                                        >
                                            { sprintf(
                                                __( 'Reset %s preset', 'advanced-gutenberg' ),
                                                currentPreset.label
                                            ) }
                                        </Button>
                                    ) }
                                    <Notice
                                        className="advgb-notice-sidebar"
                                        status="warning"
                                        isDismissible={ false }
                                    >
                                        { __( 'Selecting or resetting a preset will override your current design configuration.', 'advanced-gutenberg' ) }
                                    </Notice>
                                </PanelBody>
                                <PanelBody
                                    title={ __('Accordion Settings', 'advanced-gutenberg') }
                                    initialOpen={ false }
                                    className="advgb-pro-icon"
                                >
                                    <RangeControl
                                        label={ __( 'Font Size', 'advanced-gutenberg' ) }
                                        value={ headerSize || '' }
                                        onChange={ ( value ) => setAttributes( { headerSize: value } ) }
                                        min={ 7 }
                                        max={ 50 }
                                        beforeIcon="editor-textcolor"
                                        allowReset
                                    />
                                    <AdvFontControl
                                        familyValue={ fontFamily }
                                        familyOnChange={ ( value ) => setAttributes( { fontFamily: value, fontVariation: '' } ) }
                                        variationValue={ fontVariation }
                                        variationOnChange={ ( value ) => setAttributes( { fontVariation: value } ) }
                                        transformValue={ textTransform }
                                        transformOnChange={ ( value ) => setAttributes( { textTransform: value } ) }
                                    />
                                </PanelBody>
                            </InspectorControls>
                        </Fragment>
                    ) }
                    { ( props.name === 'advgb/accordions'
                        || props.name === 'advgb/accordion-item'
                    ) && id && (
                        <div>
                            <style>
                            { headerSize && (
                                `.${id} .advgb-accordion-header-title {
                                    font-size: ${headerSize}px !important;
                                }`
                            ) }
                            { /* Accordion Google fonts */ }
                            { <AdvSetGoogleFontStyle
                                fontFamily={ fontFamily }
                                fontVariation={ fontVariation }
                                textTransform={ textTransform }
                                cssSelector= { `.${id} .advgb-accordion-header-title` }
                            /> }
                            </style>
                            { /* Accordions Google fonts stylesheet */ }
                            <AdvLoadGoogleStylesheet
                                fontFamily={ fontFamily }
                                fontVariation={ fontVariation }
                            />
                        </div>
                    ) }
                </Fragment>
            )
        });
    } );

})( wp.i18n, wp.hooks, wp.components, wp.element, wp.blockEditor, wp.data, wp.compose );
