(function ( wpI18n, wpBlocks, wpElement, wpComponents, wpBlockEditor ) {
    wpBlockEditor = wp.blockEditor || wp.editor;
    const { __ } = wpI18n;
    const { createElement, Fragment, Component } = wpElement;
    const { registerBlockType } = wpBlocks;
    const { PanelBody, RangeControl, ToggleControl, SelectControl } = wpComponents;
    const { RichText, InspectorControls, AlignmentToolbar, BlockControls, PanelColorSettings, InnerBlocks } = wpBlockEditor;

    const previewImageData = 'data:image/png;base64,';

    class pricingTableEdit extends Component {

        constructor() {
            super( ...arguments );
        }

        componentWillMount() {
            const { attributes, setAttributes } = this.props;
            const currentBlockConfig = advgbDefaultConfig['advgb-pricing-table'];

            // No override attributes of blocks inserted before
            if (attributes.changed !== true) {
                if (typeof currentBlockConfig === 'object' && currentBlockConfig !== null) {
                    Object.keys(currentBlockConfig).map((attribute) => {
                        if (typeof attributes[attribute] === 'boolean') {
                            attributes[attribute] = !!currentBlockConfig[attribute];
                        } else {
                            attributes[attribute] = currentBlockConfig[attribute];
                        }
                    });
                }

                // Finally set changed attribute to true, so we don't modify anything again
                setAttributes( { changed: true } );
            }
        }

        componentDidMount() {
            const { attributes, setAttributes, clientId } = this.props;
            const { blockID } = attributes;

            setAttributes( { blockID: `advgb-pricing-table-${clientId}` } );
        }

        render() {
            const {
                className,
                attributes,
                setAttributes
            } = this.props;
            const {
                isPreview,
                blockID,
                alignment,
                heading,
                headingTag,
                displayHeading,
                headingBorder,
                price,
                priceTag,
                priceBorder,
                displayPrice,
                priceSub,
                borderWidth,
                borderColor,
                borderRadius,
                borderStyle,
                headingBackground,
                priceBackground,
                featuresBackground,
                headingPaddingTop,
                headingPaddingRight,
                headingPaddingBottom,
                headingPaddingLeft,
                pricePaddingTop,
                pricePaddingRight,
                pricePaddingBottom,
                pricePaddingLeft,
                featuresPaddingTop,
                featuresPaddingRight,
                featuresPaddingBottom,
                featuresPaddingLeft,
            } = attributes;

            const featuresTemplate = [
                [
                    'advgb/feature-list',
                ],
                [
                    'advgb/button',
                    {
                        text: __('Sign up', 'advanced-gutenberg'),
                        align: 'center',
                        marginTop: 30,
                        marginBottom: 30,
                        paddingTop: 6,
                        paddingRight: 15,
                        paddingBottom: 6,
                        paddingLeft: 15,
                        borderRadius: 6,
                        urlOpenNewTab: false,
                    }
                ],
            ];

            const htmlTags = [
                { label: 'h1', value: 'h1' },
                { label: 'h2', value: 'h2' },
                { label: 'h3', value: 'h3' },
                { label: 'h4', value: 'h4' },
                { label: 'h5', value: 'h5' },
                { label: 'h6', value: 'h6' },
                { label: 'p', value: 'p' },
                { label: 'div', value: 'div' },
            ];

            const listBorderStyles = [
                { label: __( 'None', 'advanced-gutenberg' ), value: 'none' },
                { label: __( 'Solid', 'advanced-gutenberg' ), value: 'solid' },
                { label: __( 'Dotted', 'advanced-gutenberg' ), value: 'dotted' },
                { label: __( 'Dashed', 'advanced-gutenberg' ), value: 'dashed' },
                { label: __( 'Double', 'advanced-gutenberg' ), value: 'double' },
                { label: __( 'Groove', 'advanced-gutenberg' ), value: 'groove' },
                { label: __( 'Ridge', 'advanced-gutenberg' ), value: 'ridge' },
                { label: __( 'Inset', 'advanced-gutenberg' ), value: 'inset' },
                { label: __( 'Outset', 'advanced-gutenberg' ), value: 'outset' },
            ];

            const blockClassName = [
                'advgb-pricing-table',
                alignment && `advgb-pricing-table-alignment__${alignment}`,
            ].filter( Boolean ).join( ' ' );

            return (
                isPreview ?
                    <img alt={ __('Pricing Table', 'advanced-gutenberg') } width='100%' src={ previewImageData }/>
                    :
                    <Fragment>
                        <BlockControls>
                            <AlignmentToolbar
                            value={ alignment } onChange={ ( value ) => setAttributes( { alignment: value === undefined ? 'center' : value } ) } />
                        </BlockControls>
                        <InspectorControls>
                            <PanelBody title={ __( 'Heading', 'advanced-gutenberg' ) } initialOpen={ false } >
                                <ToggleControl
                                    label={ __( 'Display Heading', 'advanced-gutenberg' ) }
                                    checked={ displayHeading }
                                    onChange={ () => setAttributes( { displayHeading: !displayHeading } ) }
                                />
                                { displayHeading &&
                                    <Fragment>
                                        <SelectControl
                                            label={ __( 'Heading Tag', 'advanced-gutenberg' ) }
                                            value={ headingTag }
                                            options={ htmlTags }
                                            onChange={ ( value ) => setAttributes( { headingTag: value } ) }
                                        />
                                        { borderWidth > 0 &&
                                            <ToggleControl
                                                label={ __( 'Border Bottom', 'advanced-gutenberg' ) }
                                                checked={ headingBorder }
                                                onChange={ () => setAttributes( { headingBorder: !headingBorder } ) }
                                            />
                                        }
                                        <RangeControl
                                            label={ __( 'Padding top', 'advanced-gutenberg' ) }
                                            value={ headingPaddingTop || '' }
                                            onChange={ ( value ) => setAttributes( { headingPaddingTop: value } ) }
                                            min={ 0 }
                                            max={ 100 }
                                        />
                                        <RangeControl
                                            label={ __( 'Padding right', 'advanced-gutenberg' ) }
                                            value={ headingPaddingRight || '' }
                                            onChange={ ( value ) => setAttributes( { headingPaddingRight: value } ) }
                                            min={ 0 }
                                            max={ 100 }
                                        />
                                        <RangeControl
                                            label={ __( 'Padding bottom', 'advanced-gutenberg' ) }
                                            value={ headingPaddingBottom || '' }
                                            onChange={ ( value ) => setAttributes( { headingPaddingBottom: value } ) }
                                            min={ 0 }
                                            max={ 100 }
                                        />
                                        <RangeControl
                                            label={ __( 'Padding left', 'advanced-gutenberg' ) }
                                            value={ headingPaddingLeft || '' }
                                            onChange={ ( value ) => setAttributes( { headingPaddingLeft: value } ) }
                                            min={ 0 }
                                            max={ 100 }
                                        />
                                    </Fragment>
                                }
                            </PanelBody>
                            <PanelBody title={ __( 'Price', 'advanced-gutenberg' ) }>
                                <ToggleControl
                                    label={ __( 'Display Price', 'advanced-gutenberg' ) }
                                    checked={ displayPrice }
                                    onChange={ () => setAttributes( { displayPrice: !displayPrice } ) }
                                />
                                { displayPrice &&
                                    <Fragment>
                                        <SelectControl
                                            label={ __( 'Price Tag', 'advanced-gutenberg' ) }
                                            value={ priceTag }
                                            options={ htmlTags }
                                            onChange={ ( value ) => setAttributes( { priceTag: value } ) }
                                        />
                                        { borderWidth > 0 &&
                                            <ToggleControl
                                                label={ __( 'Border Bottom', 'advanced-gutenberg' ) }
                                                checked={ priceBorder }
                                                onChange={ () => setAttributes( { priceBorder: !priceBorder } ) }
                                            />
                                        }
                                        <RangeControl
                                            label={ __( 'Padding top', 'advanced-gutenberg' ) }
                                            value={ pricePaddingTop || '' }
                                            onChange={ ( value ) => setAttributes( { pricePaddingTop: value } ) }
                                            min={ 0 }
                                            max={ 100 }
                                        />
                                        <RangeControl
                                            label={ __( 'Padding right', 'advanced-gutenberg' ) }
                                            value={ pricePaddingRight || '' }
                                            onChange={ ( value ) => setAttributes( { pricePaddingRight: value } ) }
                                            min={ 0 }
                                            max={ 100 }
                                        />
                                        <RangeControl
                                            label={ __( 'Padding bottom', 'advanced-gutenberg' ) }
                                            value={ pricePaddingBottom || '' }
                                            onChange={ ( value ) => setAttributes( { pricePaddingBottom: value } ) }
                                            min={ 0 }
                                            max={ 100 }
                                        />
                                        <RangeControl
                                            label={ __( 'Padding left', 'advanced-gutenberg' ) }
                                            value={ pricePaddingLeft || '' }
                                            onChange={ ( value ) => setAttributes( { pricePaddingLeft: value } ) }
                                            min={ 0 }
                                            max={ 100 }
                                        />
                                    </Fragment>
                                }
                            </PanelBody>
                            <PanelBody title={ __( 'Features', 'advanced-gutenberg' ) }>
                                <RangeControl
                                    label={ __( 'Padding top', 'advanced-gutenberg' ) }
                                    value={ featuresPaddingTop || '' }
                                    onChange={ ( value ) => setAttributes( { featuresPaddingTop: value } ) }
                                    min={ 0 }
                                    max={ 100 }
                                />
                                <RangeControl
                                    label={ __( 'Padding right', 'advanced-gutenberg' ) }
                                    value={ featuresPaddingRight || '' }
                                    onChange={ ( value ) => setAttributes( { featuresPaddingRight: value } ) }
                                    min={ 0 }
                                    max={ 100 }
                                />
                                <RangeControl
                                    label={ __( 'Padding bottom', 'advanced-gutenberg' ) }
                                    value={ featuresPaddingBottom || '' }
                                    onChange={ ( value ) => setAttributes( { featuresPaddingBottom: value } ) }
                                    min={ 0 }
                                    max={ 100 }
                                />
                                <RangeControl
                                    label={ __( 'Padding left', 'advanced-gutenberg' ) }
                                    value={ featuresPaddingLeft || '' }
                                    onChange={ ( value ) => setAttributes( { featuresPaddingLeft: value } ) }
                                    min={ 0 }
                                    max={ 100 }
                                />
                            </PanelBody>
                            <PanelBody title={ __( 'Border', 'advanced-gutenberg' ) } initialOpen={ false } >
                                <RangeControl
                                    label={ __( 'Border radius', 'advanced-gutenberg' ) }
                                    value={ borderRadius || '' }
                                    onChange={ ( value ) => setAttributes( { borderRadius: value } ) }
                                    min={ 0 }
                                    max={ 100 }
                                />
                                <SelectControl
                                    label={ __( 'Border style', 'advanced-gutenberg' ) }
                                    value={ borderStyle }
                                    options={ listBorderStyles }
                                    onChange={ ( value ) => setAttributes( { borderStyle: value } ) }
                                />
                                {borderStyle !== 'none' && (
                                    <Fragment>
                                        <PanelColorSettings
                                            title={ __( 'Border Color', 'advanced-gutenberg' ) }
                                            initialOpen={ false }
                                            colorSettings={ [
                                                {
                                                    label: __( 'Border Color', 'advanced-gutenberg' ),
                                                    value: borderColor,
                                                    onChange: ( value ) => setAttributes( { borderColor: value === undefined ? '#2196f3' : value } ),
                                                },
                                            ] }
                                        />
                                        <RangeControl
                                            label={ __( 'Border width', 'advanced-gutenberg' ) }
                                            value={ borderWidth || '' }
                                            onChange={ ( value ) => setAttributes( { borderWidth: value } ) }
                                            min={ 0 }
                                            max={ 100 }
                                        />
                                    </Fragment>
                                ) }
                            </PanelBody>
                            <PanelColorSettings
                                title={ __( 'Colors', 'advanced-gutenberg' ) }
                                initialOpen={ false }
                                colorSettings={ [
                                    {
                                        label: __( 'Heading Background', 'advanced-gutenberg' ),
                                        value: headingBackground,
                                        onChange: ( value ) => setAttributes( { headingBackground: value === undefined ? '#f2f2f2' : value } ),
                                    },
                                    {
                                        label: __( 'Price Background', 'advanced-gutenberg' ),
                                        value: priceBackground,
                                        onChange: ( value ) => setAttributes( { priceBackground: value === undefined ? '#fff' : value } ),
                                    },
                                    {
                                        label: __( 'Features Background', 'advanced-gutenberg' ),
                                        value: featuresBackground,
                                        onChange: ( value ) => setAttributes( { featuresBackground: value === undefined ? '#fff' : value } ),
                                    },
                                ] }
                            />
                        </InspectorControls>
                        <div className={ blockClassName }>
                            <div
                                className={ 'advgb-pricing-table-inner' }
                                id={ blockID }
                                >
                                { displayHeading &&
                                    <div className={ 'advgb-pricing-table__heading' }>
                                        <RichText
                                            tagName={ headingTag }
                                            onChange={ ( value ) => setAttributes( { heading: value } ) }
                                            value={ heading }
                                            placeholder={ __("Type a plan name", 'advanced-gutenberg') }
                                            allowedFormats={ [
                                                'core/bold',
                                                'core/italic',
                                                'core/text-color',
                                            ] }
                                        />
                                    </div>
                                }
                                { displayPrice &&
                                    <div className={ 'advgb-pricing-table__price' }>
                                        <RichText
                                            tagName={ priceTag }
                                            onChange={ ( value ) => setAttributes( { price: value } ) }
                                            value={ price }
                                            placeholder={ __("Type a price", 'advanced-gutenberg') }
                                            allowedFormats={ [
                                                'core/bold',
                                                'core/italic',
                                                'core/superscript',
                                                'core/subscript',
                                                'core/strikethrough',
                                                'core/text-color',
                                            ] }
                                        />
                                        <RichText
                                            tagName="div"
                                            onChange={ ( value ) => setAttributes( { priceSub: value } ) }
                                            value={ priceSub }
                                            className="advgb-pricing-table__pricesub"
                                            placeholder={ __("Type a price subheading", 'advanced-gutenberg') }
                                            allowedFormats={ [
                                                'core/bold',
                                                'core/italic',
                                                'core/superscript',
                                                'core/subscript',
                                                'core/strikethrough',
                                                'core/text-color',
                                            ] }
                                        />
                                    </div>
                                }
                                <div className={ 'advgb-pricing-table__features' }>
                                    <InnerBlocks
                                        template={ featuresTemplate }
                                        templateLock={ false }
                                        allowedBlocks={ [
                                            'advgb/feature-list',
                                            'core/heading',
                                            'core/paragraph',
                                            'advgb/button',
                                            'core/button',
                                            'core/spacer',
                                            'core/separator',
                                            'advgb/icon',
                                        ] }
                                    />
                                </div>
                            </div>
                        </div>
                        <style>
                            {`#${blockID} {
                                border: ${borderWidth}px ${borderStyle} ${borderColor};
                                border-radius: ${borderRadius}px;
                            }
                            #${blockID} .advgb-pricing-table__heading {
                                border-bottom: none;
                                background: ${headingBackground};
                                padding: ${headingPaddingTop}px ${headingPaddingRight}px ${headingPaddingBottom}px ${headingPaddingLeft}px;
                            }
                            #${blockID} .advgb-pricing-table__price {
                                border-bottom: none;
                                background: ${priceBackground};
                                padding: ${pricePaddingTop}px ${pricePaddingRight}px ${pricePaddingBottom}px ${pricePaddingLeft}px;
                            }
                            #${blockID} .advgb-pricing-table__features {
                                background: ${featuresBackground};
                                padding: ${featuresPaddingTop}px ${featuresPaddingRight}px ${featuresPaddingBottom}px ${featuresPaddingLeft}px;
                            }`}
                        </style>

                        { headingBorder && borderWidth > 0 &&
                            <style>
                            { `#${blockID} .advgb-pricing-table__heading {
                                border-bottom: ${borderWidth}px ${borderStyle} ${borderColor};
                            }` }
                            </style>
                        }

                        { priceBorder && borderWidth > 0 &&
                            <style>
                            { `#${blockID} .advgb-pricing-table__price {
                                border-bottom: ${borderWidth}px ${borderStyle} ${borderColor};
                            }` }
                            </style>
                        }
                    </Fragment>
            );
        }
    }

    const blockIcon = (
        <svg xmlns="http://www.w3.org/2000/svg" enable-background="new 0 0 24 24" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000">
            <g>
                <rect fill="none" height="24" width="24"/>
                <path d="M14,2H6C4.9,2,4,2.9,4,4v16c0,1.1,0.9,2,2,2h12c1.1,0,2-0.9,2-2V8L14,2z M6,20V4h7v4h5v12H6z M11,19h2v-1h1 c0.55,0,1-0.45,1-1v-3c0-0.55-0.45-1-1-1h-3v-1h4v-2h-2V9h-2v1h-1c-0.55,0-1,0.45-1,1v3c0,0.55,0.45,1,1,1h3v1H9v2h2V19z"/>
            </g>
        </svg>
    )
    const blockAttrs = {
        blockID: {
            type: 'string',
        },
        alignment: {
            type: 'string',
            default: 'center',
        },
        heading: {
            type: 'string',
            default: '\u003cspan class=\u0022has-inline-color has-white-color\u0022\u003ePlan Heading\u003c/span\u003e',
        },
        headingTag: {
            type: 'string',
            default: 'h3',
        },
        displayHeading: {
            type: 'boolean',
            default: true,
        },
        headingBorder: {
            type: 'boolean',
            default: false,
        },
        price: {
            type: 'string',
            default: '\u003cspan class=\u0022has-inline-color has-white-color\u0022\u003e$99\u003c/span\u003e',
        },
        priceTag: {
            type: 'string',
            default: 'h3',
        },
        priceBorder: {
            type: 'boolean',
            default: false,
        },
        displayPrice: {
            type: 'boolean',
            default: true,
        },
        priceSub: {
            type: 'string',
            default: '\u003csup\u003e\u003cspan class=\u0022has-inline-color has-white-color\u0022\u003eyearly\u003c/span\u003e\u003c/sup\u003e'
        },
        borderWidth: {
            type: 'number',
            default: 2,
        },
        borderColor: {
            type: 'string',
            default: '#2196f3',
        },
        borderRadius: {
            type: 'number',
            default: 0,
        },
        borderStyle: {
            type: 'string',
            default: 'solid',
        },
        headingBackground: {
            type: 'string',
            default: '#2196f3',
        },
        priceBackground: {
            type: 'string',
            default: '#82c2f7',
        },
        featuresBackground: {
            type: 'string',
            default: '#ffffff',
        },
        headingPaddingTop: {
            type: 'number',
            default: 20,
        },
        headingPaddingRight: {
            type: 'number',
            default: 20,
        },
        headingPaddingBottom: {
            type: 'number',
            default: 20,
        },
        headingPaddingLeft: {
            type: 'number',
            default: 20,
        },
        pricePaddingTop: {
            type: 'number',
            default: 20,
        },
        pricePaddingRight: {
            type: 'number',
            default: 20,
        },
        pricePaddingBottom: {
            type: 'number',
            default: 20,
        },
        pricePaddingLeft: {
            type: 'number',
            default: 20,
        },
        featuresPaddingTop: {
            type: 'number',
            default: 0,
        },
        featuresPaddingRight: {
            type: 'number',
            default: 0,
        },
        featuresPaddingBottom: {
            type: 'number',
            default: 0,
        },
        featuresPaddingLeft: {
            type: 'number',
            default: 0,
        },
        changed: {
            type: 'boolean',
            default: false,
        },
        isPreview: {
            type: 'boolean',
            default: false,
        },
    };

    registerBlockType('advgb/pricing-table', {
        title: __('Pricing Table - PublishPress', 'advanced-gutenberg'),
        description: __('Easily create a customizable pricing table.', 'advanced-gutenberg'),
        icon: {
            src: blockIcon,
            foreground: typeof advgbBlocks !== 'undefined' ? advgbBlocks.color : undefined,
        },
        category: 'advgb-category',
        keywords: [
            __( 'pricing table', 'advanced-gutenberg' ),
            __( 'pricing', 'advanced-gutenberg' ),
            __( 'table', 'advanced-gutenberg' ),
        ],
        attributes: blockAttrs,
        example: {
            attributes: {
                isPreview: true
            },
        },
        supports: {
            anchor: true,
        },
        edit: pricingTableEdit,
        save: function ( { attributes } ) {
            const {
                className,
                blockID,
                alignment,
                heading,
                headingTag,
                displayHeading,
                headingBorder,
                price,
                priceTag,
                priceBorder,
                displayPrice,
                priceSub,
                borderWidth,
                borderColor,
                borderRadius,
                borderStyle,
                headingBackground,
                priceBackground,
                featuresBackground,
                headingPaddingTop,
                headingPaddingRight,
                headingPaddingBottom,
                headingPaddingLeft,
                pricePaddingTop,
                pricePaddingRight,
                pricePaddingBottom,
                pricePaddingLeft,
                featuresPaddingTop,
                featuresPaddingRight,
                featuresPaddingBottom,
                featuresPaddingLeft,
            } = attributes;

            const blockClassName = [
                'advgb-pricing-table',
                alignment && `advgb-pricing-table-alignment__${alignment}`,
            ].filter( Boolean ).join( ' ' );

            return (
                <div className={ blockClassName }>
                    <div
                    className={ 'advgb-pricing-table-inner' }
                    id={ blockID }>
                        { displayHeading &&
                            <div className={ 'advgb-pricing-table__heading' }>
                                <RichText.Content
                                    tagName={ headingTag }
                                    value={ heading }
                                />
                            </div>
                        }
                        { displayPrice &&
                            <div className={ 'advgb-pricing-table__price' }>
                                <RichText.Content
                                    tagName={ priceTag }
                                    value={ price }
                                />
                                <RichText.Content
                                    tagName="div"
                                    value={ priceSub }
                                    className="advgb-pricing-table__pricesub"
                                />
                            </div>
                        }
                        <div className={ 'advgb-pricing-table__features' }>
                            <InnerBlocks.Content />
                        </div>
                    </div>
                </div>
            );
        },
    });
})( wp.i18n, wp.blocks, wp.element, wp.components, wp.blockEditor );
