(function ( wpI18n, wpHooks, wpBlockEditor, wpComponents, wpElement ) {
    wpBlockEditor = wp.blockEditor || wp.editor;
    const { addFilter } = wpHooks;
    const { __ } = wpI18n;
    const { InspectorControls } = wpBlockEditor;
    const { PanelBody, SelectControl } = wpComponents;
    const { Fragment } = wpElement;

    const htmlTags = [
        { label: 'h1', value: 'h1' },
        { label: 'h2', value: 'h2' },
        { label: 'h3', value: 'h3' },
        { label: 'h4', value: 'h4' },
        { label: 'h5', value: 'h5' },
        { label: 'h6', value: 'h6' },
        { label: 'p', value: 'p' },
        { label: 'div', value: 'div' },
    ];

    // Add fields to sidebar
    addFilter( 'editor.BlockEdit', 'advgb/advImagePro', function ( BlockEdit ) {
        return ( props ) => {
            return (
                <Fragment>
                    <BlockEdit {...props} />
                    { props.isSelected && props.name === 'advgb/image' && (
                        <Fragment>
                            <InspectorControls>
                                <PanelBody title={ __('Content Tags', 'advanced-gutenberg') } className="advgb-pro-icon">
                                    <SelectControl
                                        label={ __( 'Title tag', 'advanced-gutenberg' ) }
                                        value={ props.attributes.titleTag }
                                        options={ htmlTags }
                                        onChange={ ( value ) => props.setAttributes( { titleTag: value } ) }
                                    />
                                    <SelectControl
                                        label={ __( 'Subtitle tag', 'advanced-gutenberg' ) }
                                        value={ props.attributes.subtitleTag }
                                        options={ htmlTags }
                                        onChange={ ( value ) => props.setAttributes( { subtitleTag: value } ) }
                                    />
                                </PanelBody>
                            </InspectorControls>
                        </Fragment>
                    ) }
                </Fragment>
            )
        }
    } );

})( wp.i18n, wp.hooks, wp.blockEditor, wp.components, wp.element );
