import { AdvFontControl, AdvLoadGoogleStylesheet, AdvSetGoogleFontStyle } from "../0-adv-components/fonts.jsx";

(function ( wpI18n, wpHooks, wpBlockEditor, wpComponents, wpElement ) {
    wpBlockEditor = wp.blockEditor || wp.editor;
    const { addFilter } = wpHooks;
    const { __ } = wpI18n;
    const { InspectorControls } = wpBlockEditor;
    const { PanelBody, SelectControl, ToggleControl, RangeControl } = wpComponents;
    const { Fragment } = wpElement;

    const htmlTags = [
        { label: 'h1', value: 'h1' },
        { label: 'h2', value: 'h2' },
        { label: 'h3', value: 'h3' },
        { label: 'h4', value: 'h4' },
        { label: 'h5', value: 'h5' },
        { label: 'h6', value: 'h6' },
        { label: 'p', value: 'p' },
        { label: 'div', value: 'div' },
    ];

    // Set attributes
    addFilter( 'blocks.registerBlockType', 'advgb/registerImagesSliderProClass', function ( settings ) {
        if ( settings.name === 'advgb/images-slider' ) {
            settings.attributes = Object.assign( settings.attributes, {
                titleFontFamily: {
                    type: 'string',
                },
                titleFontVariation: {
                    type: 'string',
                },
                textFontFamily: {
                    type: 'string',
                },
                textFontVariation: {
                    type: 'string',
                },
            } );
        }

        return settings;
    } );

    // Add fields to sidebar
    addFilter( 'editor.BlockEdit', 'advgb/imagesSliderPro', function ( BlockEdit ) {
        return ( props ) => {
            return (
                <Fragment>
                    <BlockEdit {...props} />
                    { props.isSelected && props.name === 'advgb/images-slider' && (
                        <Fragment>
                            <InspectorControls>
                                <PanelBody title={ __('Autoplay', 'advanced-gutenberg') } initialOpen={ true } className="advgb-pro-icon">
                                    <ToggleControl
                                        label={ __( 'Autoplay', 'advanced-gutenberg' ) }
                                        checked={ props.attributes.autoplay }
                                        onChange={ () => props.setAttributes( { autoplay: !props.attributes.autoplay } ) }
                                    />
                                    { props.attributes.autoplay && (
                                        <RangeControl
                                            label={ __( 'Autoplay Speed', 'advanced-gutenberg' ) }
                                            help={ __( 'Change interval between slides in miliseconds.', 'advanced-gutenberg' ) }
                                            min={ 1000 }
                                            max={ 20000 }
                                            value={ props.attributes.autoplaySpeed }
                                            onChange={ (value) => props.setAttributes( { autoplaySpeed: value } ) }
                                        />
                                    ) }
                                </PanelBody>
                                <PanelBody title={ __('Title', 'advanced-gutenberg') } initialOpen={ false } className="advgb-pro-icon">
                                    { props.attributes.titleShow &&  (
                                        <Fragment>
                                            <SelectControl
                                                label={ __( 'Tag', 'advanced-gutenberg' ) }
                                                value={ props.attributes.titleTag }
                                                options={ htmlTags }
                                                onChange={ ( value ) => props.setAttributes( { titleTag: value } ) }
                                            />
                                            <AdvFontControl
                                                familyValue={ props.attributes.titleFontFamily }
                                                familyOnChange={ ( value ) => props.setAttributes( { titleFontFamily: value, titleFontVariation: '' } ) }
                                                variationValue={ props.attributes.titleFontVariation }
                                                variationOnChange={ ( value ) => props.setAttributes( { titleFontVariation: value } ) }
                                            />
                                        </Fragment>
                                    ) }
                                    <ToggleControl
                                        label={ __( 'Display', 'advanced-gutenberg' ) }
                                        help={ __( 'Disable this if you want to include the Title as alt attribute without displaying the title over the image.', 'advanced-gutenberg' ) }
                                        checked={ props.attributes.titleShow }
                                        onChange={ () => props.setAttributes( { titleShow: !props.attributes.titleShow } ) }
                                    />
                                </PanelBody>
                                <PanelBody title={ __('Text', 'advanced-gutenberg') } initialOpen={ false } className="advgb-pro-icon">
                                    <SelectControl
                                        label={ __( 'Tag', 'advanced-gutenberg' ) }
                                        value={ props.attributes.textTag }
                                        options={ htmlTags }
                                        onChange={ ( value ) => props.setAttributes( { textTag: value } ) }
                                    />
                                    <AdvFontControl
                                        familyValue={ props.attributes.textFontFamily }
                                        familyOnChange={ ( value ) => props.setAttributes( { textFontFamily: value, textFontVariation: '' } ) }
                                        variationValue={ props.attributes.textFontVariation }
                                        variationOnChange={ ( value ) => props.setAttributes( { textFontVariation: value } ) }
                                    />
                                </PanelBody>
                            </InspectorControls>
                        </Fragment>
                    ) }
                    { props.name === 'advgb/images-slider' && props.attributes.id && (
                        <div>
                            <style>
                                { /* Title CSS */ <AdvSetGoogleFontStyle
                                    fontFamily={ props.attributes.titleFontFamily }
                                    fontVariation={ props.attributes.titleFontVariation }
                                    cssSelector= { `.${props.attributes.id} .advgb-image-slider-item .advgb-image-slider-item-info .advgb-image-slider-title` }
                                /> }
                                { /* Text CSS */ <AdvSetGoogleFontStyle
                                    fontFamily={ props.attributes.textFontFamily }
                                    fontVariation={ props.attributes.textFontVariation }
                                    cssSelector= { `.${props.attributes.id} .advgb-image-slider-item .advgb-image-slider-item-info .advgb-image-slider-text` }
                                /> }
                            </style>
                            { /* Title Google font stylesheet */ }
                            <AdvLoadGoogleStylesheet
                                fontFamily={ props.attributes.titleFontFamily }
                                fontVariation={ props.attributes.titleFontVariation }
                            />
                            { /* Text Google font stylesheet */ }
                            <AdvLoadGoogleStylesheet
                                fontFamily={ props.attributes.textFontFamily }
                                fontVariation={ props.attributes.textFontVariation }
                            />
                        </div>
                    ) }
                </Fragment>
            )
        }
    } );

})( wp.i18n, wp.hooks, wp.blockEditor, wp.components, wp.element );
