import { AdvFontControl, AdvLoadGoogleStylesheet, AdvSetGoogleFontStyle } from "../0-adv-components/fonts.jsx";

(function ( wpI18n, wpHooks, wpBlockEditor, wpComponents, wpElement ) {
    wpBlockEditor = wp.blockEditor || wp.editor;
    const { addFilter } = wpHooks;
    const { __ } = wpI18n;
    const { InspectorControls } = wpBlockEditor;
    const { PanelBody, SelectControl, ToggleControl } = wpComponents;
    const { Fragment } = wpElement;

    // Set attributes
    addFilter( 'blocks.registerBlockType', 'advgb/registerAdvButtonProClass', function ( settings ) {
        if ( settings.name === 'advgb/button' ) {
            settings.attributes = Object.assign( settings.attributes, {
                fontFamily: {
                    type: 'string',
                },
                fontVariation: {
                    type: 'string',
                }
            } );
        }

        return settings;
    } );

    // Add fields to sidebar
    addFilter( 'editor.BlockEdit', 'advgb/advButtonPro', function ( BlockEdit ) {
        return ( props ) => {
            return (
                <Fragment>
                    <BlockEdit {...props} />
                    { props.isSelected && props.name === 'advgb/button' && (
                        <Fragment>
                            <InspectorControls>
                                <PanelBody title={ __('Font', 'advanced-gutenberg') } initialOpen={ false } className="advgb-pro-icon">
                                    <AdvFontControl
                                        familyValue={ props.attributes.fontFamily }
                                        familyOnChange={ ( value ) => props.setAttributes( { fontFamily: value, fontVariation: '' } ) }
                                        variationValue={ props.attributes.fontVariation }
                                        variationOnChange={ ( value ) => props.setAttributes( { fontVariation: value } ) }
                                    />
                                </PanelBody>
                                <PanelBody title={ __('Link rel', 'advanced-gutenberg') } initialOpen={ false } className="advgb-pro-icon">
                                    <ToggleControl
                                        label="noreferrer"
                                        checked={ props.attributes.noreferrer }
                                        onChange={ () => props.setAttributes( { noreferrer: !props.attributes.noreferrer } ) }
                                    />
                                    <ToggleControl
                                        label="nofollow"
                                        checked={ props.attributes.nofollow }
                                        onChange={ () => props.setAttributes( { nofollow: !props.attributes.nofollow } ) }
                                    />
                                </PanelBody>
                            </InspectorControls>
                        </Fragment>
                    ) }
                    { props.name === 'advgb/button' && props.attributes.id && (
                        <div>
                            <style>
                                { <AdvSetGoogleFontStyle
                                    fontFamily={ props.attributes.fontFamily }
                                    fontVariation={ props.attributes.fontVariation }
                                    cssSelector= { `.${props.attributes.id} span` }
                                /> }
                            </style>
                            <AdvLoadGoogleStylesheet
                                fontFamily={ props.attributes.fontFamily }
                                fontVariation={ props.attributes.fontVariation }
                            />
                        </div>
                    ) }
                </Fragment>
            )
        }
    } );

})( wp.i18n, wp.hooks, wp.blockEditor, wp.components, wp.element );
