(function ( wpI18n, wpHooks, wpBlockEditor, wpComponents, wpElement ) {
    wpBlockEditor = wp.blockEditor || wp.editor;
    const { addFilter } = wpHooks;
    const { __ } = wpI18n;
    const { InspectorControls, BlockControls, AlignmentToolbar } = wpBlockEditor;
    const { PanelBody, RangeControl } = wpComponents;
    const { Fragment } = wpElement;

    // Set attributes
    addFilter( 'blocks.registerBlockType', 'advgb/registerCountUpProClass', function ( settings ) {
        if ( settings.name === 'advgb/count-up' ) {
            settings.attributes = Object.assign( settings.attributes, {
                headerSize: {
                    type: 'number'
                },
                descriptionSize: {
                    type: 'number'
                },
                align: {
                    type: 'string',
                    default: 'center'
                }
            } );
        }

        return settings;
    } );

    // Add fields to sidebar
    addFilter( 'editor.BlockEdit', 'advgb/countUpPro', function ( BlockEdit ) {
        return ( props ) => {
            return (
                <Fragment>
                    <BlockEdit {...props} />
                    { props.isSelected && props.name === 'advgb/count-up' && (
                    <Fragment>
                        <BlockControls>
                            <AlignmentToolbar
                                value={ props.attributes.align }
                                onChange={ ( value ) => props.setAttributes( { align: value } ) }
                            />
                        </BlockControls>
                        <InspectorControls>
                            <PanelBody title={ __('Header & Description', 'advanced-gutenberg') } className="advgb-pro-icon">
                                <RangeControl
                                    label={ __( 'Header size', 'advanced-gutenberg' ) }
                                    value={ props.attributes.headerSize || '' }
                                    onChange={ ( value ) => props.setAttributes( { headerSize: value } ) }
                                    min={ 10 }
                                    max={ 100 }
                                    beforeIcon="editor-textcolor"
                                    allowReset
                                />
                                <RangeControl
                                    label={ __( 'Description size', 'advanced-gutenberg' ) }
                                    value={ props.attributes.descriptionSize || '' }
                                    onChange={ ( value ) => props.setAttributes( { descriptionSize: value } ) }
                                    min={ 10 }
                                    max={ 100 }
                                    beforeIcon="editor-textcolor"
                                    allowReset
                                />
                            </PanelBody>
                        </InspectorControls>
                    </Fragment>
                    ) }
                    { props.name === 'advgb/count-up' && props.attributes.id && (
                        <div>
                            <style>
                            { props.attributes.headerSize && (
                                `.${props.attributes.id} .advgb-count-up-header {
                                    font-size: ${props.attributes.headerSize}px;
                                }`
                            ) }
                            { props.attributes.descriptionSize && (
                                `.${props.attributes.id} .advgb-count-up-desc {
                                    font-size: ${props.attributes.descriptionSize}px;
                                }`
                            ) }
                            { props.attributes.align && (
                                `.${props.attributes.id}.advgb-count-up > div {
                                    text-align: ${props.attributes.align};
                                }`
                            ) }
                            </style>
                        </div>
                    ) }
                </Fragment>
            )
        }
    } );

})( wp.i18n, wp.hooks, wp.blockEditor, wp.components, wp.element );
